::-webkit-scrollbar {
    width: 5px;
    background-color: transparent;
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: #999;
    border-radius: 10px;
    transition: background-color 0.2s;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background-color: #555;
  }
  
  ::-webkit-scrollbar-thumb:active {
    background-color: #333;
  }
  